import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import { Instance } from 'mobx-state-tree'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { BiCheckCircle, BiCircle } from 'react-icons/bi'
import { RouteComponentProps, useLocation } from 'react-router'
import { FooterFormulario, PageApp } from '../../components'
import { ModalInfo } from '../../components/ModalInfo'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import { useApiToaster, useTryGoBack } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import Vehiculo from '../../models/types/Vehiculo'
import './VehiculosTransportista.css'

export const VehiculosTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore, loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [ModalActivar, setModalActivar] = useState(false)
  const [ModalEditar, setModalEditar] = useState(false)
  const [Seleccionado, setSeleccionado] = useState<null | Instance<typeof Vehiculo>>()
  const tryGoBack = useTryGoBack()
  const apiToast = useApiToaster()
  const { search } = useLocation()

  const setup = () => {
    if (loginStore.transportista) {
      obtenerTransportista(loginStore.transportista.id)
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  React.useEffect(() => {
    const currentQuery = new URLSearchParams(search)

    if (currentQuery.get('r')) {
      setup()
    }
  }, [search])

  const obtenerTransportista = (idTransportista: number) => {
    setLoading(true)
    // @ts-ignore
    rootStore.environment.api.adminGetTransportista(idTransportista).then((result) => {
      if (result.kind === 'ok') {
        transportistaStore.setTransportista(result.transportista)
      }
      setLoading(false)
    })
  }

  const activarVehiculo = (idVehiculo: number) => {
    setLoading(true)
    // @ts-ignore
    rootStore.environment.api.activarVehiculo(idVehiculo).then((result) => {
      setLoading(false)
      apiToast(result, 'Vehículo activado', 'al activar')
      if (result.kind === 'ok') {
        history.replace(RUTAS_APP.HOME)
      }
    })
  }

  return (
    <PageApp
      preset={'p-driver'}
      subBarraExternaHeader={
        <SubHeader
          links={[
            { titulo: 'Perfil', ruta: RUTAS_APP.PERFIL },
            { titulo: 'Configuración', ruta: RUTAS_APP.CONFIGURACION },
            { titulo: 'Rodados' },
          ]}
        />
      }
      sub_titulo={` `}
      mostrar_barra={false}
    >
      {!!Seleccionado && (
        <>
          <ModalInfo
            isOpen={ModalActivar}
            color={'var(--rojo-abi)'}
            titulo={'Seleccionar - ' + Seleccionado.modelo}
            cssClass="modal-activar-vehiculo-transportista"
            close={() => setModalActivar(false)}
          >
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <button
                className="btn btn-outline-danger"
                style={{ padding: 10 }}
                onClick={() => {
                  activarVehiculo(Seleccionado.id)
                }}
              >
                {Loading && <IonSpinner name="crescent" />}
                {!Loading && <span>Activar</span>}
              </button>
            </div>
          </ModalInfo>
          <ModalInfo
            isOpen={ModalEditar}
            color={'var(--rojo-abi)'}
            titulo={'Editar rodado'}
            cssClass="modal-editar-vehiculo-transportista"
            close={() => setModalEditar(false)}
          >
            <div style={{ display: 'flex', flexDirection: 'column', gap: 15 }}>
              <button className="btn btn-outline-secondary" style={{}} onClick={() => {}}>
                {Loading && <IonSpinner name="crescent" />}
                {!Loading && (
                  <>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                      <FIcon.Trash size={20} />
                      <span>Eliminar</span>
                    </div>
                  </>
                )}
              </button>
              <button
                className="btn-principal-transportista"
                style={{}}
                onClick={() => {
                  transportistaStore.setVehiculoDetalle(Seleccionado)
                  history.push(RUTAS_APP.VEHICULO)
                }}
              >
                {Loading && <IonSpinner name="crescent" />}
                {!Loading && <span>Ver detalles</span>}
              </button>
            </div>
          </ModalInfo>
        </>
      )}

      <div className={'vehiculos-transportista-list-container py-2'}>
        <h4>Rodados</h4>
        <div className="d-flex justify-content-center">
          <p>Seleccioná el rodado con el que vas a trabajar</p>{' '}
        </div>
        <div className={'vehiculos-transportista-list-container py-2'}>
          {transportistaStore.vehiculos.map((item, index) => {
            let icono = ''

            if (item.tipo_vehiculo.nombre === 'Auto') {
              icono = 'assets/icon/car'
            } else if (item.tipo_vehiculo.nombre === 'Moto') {
              icono = 'assets/icon/moto'
            } else {
              icono = 'assets/icon/truck'
            }

            return (
              <div key={'vehiculo-' + index} onClick={() => setSeleccionado(item)}>
                <div
                  className={
                    item.aprobado
                      ? 'col-12 vehiculos-transportista-list-item'
                      : 'col-12 vehiculos-transportista-list-item-disabled vehiculos-transportista-list-item'
                  }
                  style={{ display: 'flex', gap: 10, alignContent: 'center' }}
                >
                  <div
                    onClick={
                      item.aprobado
                        ? () => {
                            setModalActivar(true)
                          }
                        : () => {}
                    }
                  >
                    {item.activo && <BiCheckCircle color={'var(--verde-claro)'} />}
                    {!item.activo && <BiCircle color={'var(--gris-oscuro-abi)'} />}
                  </div>
                  <img className="icon-container-vehiculos" src={item.activo ? icono + '_green.png' : icono + '.png'} />

                  <div style={{ flex: 1 }}>
                    <div className={'item-header'}>
                      <span>
                        <strong style={item.activo ? { color: 'var(--verde-claro)' } : {}}>
                          {item.marca} {item.modelo}
                        </strong>
                        {item.aprobado ? null : '(Pendiente)'}
                      </span>
                    </div>
                    <div className={'item-body'}>
                      <span>
                        {item.numero_chapa}
                        <br />
                      </span>
                    </div>
                  </div>
                  <div className="icon-container-vehiculos" onClick={() => setModalEditar(true)}>
                    <FIcon.Edit size={15} />
                  </div>
                </div>
              </div>
            )
          })}
        </div>
        <FooterFormulario className="py-4">
          <div
            style={{
              display: 'grid',
              gridTemplateColumns: '1fr 3fr 1fr',
              width: '100%',
              maxWidth: 1200,
              gap: 15,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            <div />
            <button className={'btn-principal-transportista'} onClick={() => history.push(RUTAS_APP.VEHICULO_CREATE)}>
              Añadir
            </button>
            <div />
          </div>
        </FooterFormulario>
      </div>
    </PageApp>
  )
})

export default VehiculosTransportista
